import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import * as io from 'socket.io-client';
import { User } from '../../models/user';
import { Follow } from '../../models/follow';
import { Message } from '../../models/message';
import { UserService } from '../../services/user.service';
import { FollowService } from '../../services/follow.service';
import { MessageService } from '../../services/message.service';
// import { UploadService } from '../../services/upload.service';
import { GLOBAL } from '../../services/global';


@Component({
  selector: 'add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css'],
  providers: [
    FollowService,
    MessageService,
    UserService
  ],
})
export class AddComponent implements OnInit {
  public title: string;
  public identity;
  public token;
  public page;
  public totalPages;
  public status;
  public follows;
  public receptor;
  public viewMore;
  public url;
  public message: Message;
  public messages;
  public selectedUser;
  public showPanel;
  public loading = false;
  socket: SocketIOClient.Socket;
  constructor(
    private _messageService: MessageService,
    private _route: ActivatedRoute,
    private _router: Router,
    private _followService: FollowService,
    private _userService: UserService
  ) {
    this.title = "Nuevo mensaje";
    this.identity = _userService.getIdentity();
    this.token = _userService.getToken();
    this.url = GLOBAL.url;
    this.message = new Message('', '', '', '', '', this.identity._id);
    this.socket = io.connect();
    this.page = 1;
    this.loading = true;

  }

  ngOnInit() {
    this.getMyFollows();
    this.messages = [];

    /*Se ejecuta cada vez que se recibe un mensaje*/
    this.socket.on('refreshChat', (data: any) => {
      console.log(data);
      // this.getConversation(this.receptor, 1, true, false);
      if (data.emiter._id == this.receptor && data.receiver._id == this.identity._id) {
        // this.getConversation(this.receptor, 1, true, false);
        this.messages.push(data);

        if ("#text") $("#text").focus();
        //scroll abajo        
        var panel = document.getElementById("panel");
        panel.scrollTop = panel.scrollHeight;
        // .panel-body
      }
      if ("#text") $("#text").focus();
      //scroll abajo        
      // document.getElementById("panel").scrollIntoView(false);
      var panel = document.getElementById("panel");
      panel.scrollTop = panel.scrollHeight;
    });

  }

  getMyFollows() {
    this._followService.getMyFollows(this.token).subscribe(response => {
        // console.log(response);
        this.follows = response.follows;
      },
      error => {
        console.log(error);
      })
  }

  getMessages() {
    this._messageService.getMessages(this.token, this.page).subscribe(response => {
      console.log(response);
    }, error => {
      console.log(error);
    })
  }

  /*Obtiene las conversaciones entre el usuario identificado y un seguidor*/
  getConversation(user_id, page = 1, isAdd = false, viewMore = false) {
    this.page = page;
    if (!this.receptor) {
      this.showPanel = true;
      this.receptor = user_id;
    }
    else {
      if ((this.receptor == user_id && !viewMore) && !isAdd) {
        this.showPanel = false;
      }
      else {
        this.receptor = user_id;
        this.showPanel = true;
      }
    }

    this._messageService.getConversation(this.token, user_id, this.page).subscribe(
      response => {
        if (this.viewMore) {
          response.messages.forEach((message) => {
            this.messages.unshift(message);
          });
          this.viewMore = false;
        }
        else {
          this.messages = response.messages.reverse();
          if (this.loading) {
            setTimeout(function() {
              var panel = document.getElementById("panel");
              panel.scrollTop = panel.scrollHeight;
              if ("#text") $("#text").focus();
              this.loading = false;
            }, 1000)
          }
          else {
            var panel = document.getElementById("panel");
            panel.scrollTop = panel.scrollHeight;
            if ("#text") $("#text").focus();
          }

        }
        this.totalPages = response.pages;
        var panel = document.getElementById("panel");
        panel.scrollTop = panel.scrollHeight;
        if ("#text") $("#text").focus();
      },
      error => {
        console.log(error);
      })

  }

  /*Se envía el nueuvo mensaje*/
  sendForm(form) {
    this.message.receiver = this.receptor;
    this.message.emiter = this.identity._id;
    console.log("el ID del emisor es " + this.identity._id);
    this._messageService.addMessage(this.token, this.message).subscribe(response => {
        this.status = "success";
        form.reset();
        var message: any = null;
        if (response.message) {
          message = response.message;
        }
        else {
          message = this.message;
        }

        this.socket.emit('addNewMessage', message);
        this.messages.push(response.message);

        if ("#text") $("#text").focus();
        //scroll abajo        
        var panel = document.getElementById("panel");
        panel.scrollTop = panel.scrollHeight;

      },
      error => {
        console.log(error);
      })
  }

}



