import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MainComponent } from './main/main.component';
import { AddComponent } from './add/add.component';
import { ReceivedComponent } from './received/received.component';
import { SentComponent } from './sent/sent.component';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';


/*Rutas*/
const messageRoutes: Routes = [
    // {path : '', component: HomeComponent},
    // {path : 'login', component: LoginComponent},
    // {path : 'registro', component: RegisterComponent},
    // {path : 'mis-datos', component: UserEditComponent},
    // {path : 'gente/:page', component: UsersComponent},
    // {path : 'gente', component: UsersComponent},
    // {path : 'home', component: HomeComponent},
    // {path : 'timeline', component: TimelineComponent},
    // {path : 'perfil/:id', component: ProfileComponent},
    // {path : 'siguiendo/:id/:page', component: FollowingComponent},
    // {path : 'seguidores/:id/:page', component: FollowedComponent},
    {path: 'mensajes', component: MainComponent,
    children: [
      {path: '', redirectTo: 'recibidos', pathMatch: 'full'},
      {path: 'enviar', component : AddComponent},
      {path: 'recibidos', component : ReceivedComponent},
      {path: 'enviados', component : SentComponent},
      ]
      
    }
];

@NgModule({
  imports: [
    RouterModule.forChild(
      messageRoutes
    ),
    CommonModule,
    FormsModule
  ],
  declarations: [MainComponent, AddComponent, ReceivedComponent, SentComponent],
  exports: [
    MainComponent, AddComponent, ReceivedComponent, SentComponent
  ],
  providers: []
})
export class MessagesModule {}



