import { Component, OnInit } from '@angular/core';
import { Publication } from '../models/publication';
import { UserService } from '../services/user.service';
import { UploadService } from '../services/upload.service';
import { PublicationService } from '../services/publication.service';
import { GLOBAL } from '../services/global';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-timeline',
  templateUrl: './timeline.component.html',
  styleUrls: ['./timeline.component.css'],
  providers: [UserService, PublicationService]
})
export class TimelineComponent implements OnInit {
  public identity;
  public token;
  public total;
  public pages;
  public itemsPerPage;
  public page:number;
  public title:string;
  public url:string;
  public publications: Publication[];
  
  constructor(
    private _router:Router,
    private _route:ActivatedRoute,
    private _userService:UserService,
    private _publicationService:PublicationService
    ) {
      this.identity = this._userService.getIdentity();
      this.token = this._userService.getToken();
      this.title = "Publicaciones";
      this.url = GLOBAL.url;
      this.page = 1;
    }

  ngOnInit() {
    this.getPublications(this.page, false);
  }
  
  getPublications(page,adding){
    this._publicationService.getPublications(this.token, page).subscribe(response=>{
      this.total = response.total_items;
        this.pages = response.pages;
        this.itemsPerPage = response.items_per_page;
      if(!adding){
        this.publications = response.publications;
        console.log('getPublications invocando a la pagina ---> ' + page);
      }else{
        let arrayA = this.publications;
        let arrayB = response.publications;
        this.publications = arrayA.concat(arrayB);
        $("html, body").animate({scrollTop : $("body").prop("scrollHeight")}, 600);
      }
    },error=>{
      console.log(error);
    });
  }
  public noMore = false;
  viewMore(){
      if(this.publications.length == this.total){
      this.noMore = true;
      $("html, body").animate({scrollTop : $("body").prop("scrollHeight")}, 600);
    }else{
      this.page +=1;
      this.getPublications(this.page, true);
    }
  }
  
  refresh(event){
    this.getPublications(1, false);
    this.noMore = false;
  }
  
  public publicationToDelete: Publication;
  
  setPublicationToDelete(publicacion){
    this.publicationToDelete = publicacion;
  }
  
  borrarPublicacion(publication_id){
    console.log("el id de la publicacion a bores ---> " + this.publicationToDelete);
    this._publicationService.deletePublication(this.token, this.publicationToDelete).subscribe(response=>{
      this.getPublications(1, false);
      this.noMore = false;
    },
    error=>{
      console.log(error);
    })
  }

}



