import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { User } from '../models/user';
import { Publication } from '../models/publication';
import { UserService } from '../services/user.service';
import { PublicationService } from '../services/publication.service';
import { UploadService } from '../services/upload.service';
import { GLOBAL } from '../services/global';

@Component({
  selector: 'sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css'],
  providers: [UserService, PublicationService, UploadService]
})
export class SidebarComponent implements OnInit {
  public identity;
  public stats;
  public url;
  public token;
  public status;
  public publication: Publication;
  // public canvas: <HTMLCanvasElement>;

  /*Output*/
  @Output() sent = new EventEmitter();

  constructor(
    private _userService: UserService,
    private _publicationService: PublicationService,
    private _route: ActivatedRoute,
    private _router: Router,
    private _uploadService: UploadService
  ) {
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.stats = this._userService.getStats();
    this.url = GLOBAL.url;
    this.publication = new Publication('', '', '', '', this.identity._id);

  }

  ngOnInit() {


  }
  sendForm(form, event) {
    this._publicationService.addPublication(this.token, this.publication).subscribe(
    response => {
      if (response.publicacionGuardada) {
        this.publication = response.publicacionGuardada;
        if (this.filesToUpload) {
          // this.ResizeImage();

          var arrayAux = [];
          // this.filesToUpload[0] = document.getElementById("output");
          this._uploadService.makeFileRequest(this.url + 'upload-image-pub/' + response.publicacionGuardada._id, [], this.filesToUpload, this.token, 'image')
            .then((result) => {
              //this.publication.file = result.image;
              console.log(result)
              this.status = "success";
              setTimeout(() => this.status = false, 2250);
              form.reset();
              this._router.navigate(['/timeline']);
              $('#newPublicationModal').modal('hide');
              this.sent.emit({ sent: true });
            });
        }
        else {
          this.status = "success";
          setTimeout(() => this.status = false, 2250);
          form.reset();
          this._router.navigate(['/timeline']);
          $('#newPublicationModal').modal('hide');
          this.sent.emit({ sent: true });
        }

      }
      else {
        this.status = "error";
        setTimeout(() => this.status = false, 2250);
      }
    }, 
    error => {
      this.status = "error";
      setTimeout(() => this.status = false, 2250);
      console.log(error);
    })
  }


  // sendPublication(event){
  //   this.sent.emit({sent:true});
  // }

  public filesToUpload: Array < File > ;
  fileChangeEvent(fileInput: any) {
    this.filesToUpload = < Array < File >> fileInput.target.files;
    
  }

}



