import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Message } from '../models/message';
import { GLOBAL } from './global';

@Injectable()
export class MessageService {
    public url: string;

    constructor(public _http: HttpClient) {
        this.url = GLOBAL.url;
    }
   
    addMessage(token, message):Observable<any>{
        let params = JSON.stringify(message);
        let headers = new HttpHeaders().set('Content-Type', 'application/json').set('Authorization', token);
        
        return this._http.post(this.url + 'message', params, {headers: headers})
    }
    
    getMessages(token, page = 1):Observable<any>{
     
        let headers = new HttpHeaders().set('Content-Type', 'application/json').set('Authorization', token);
        
        return this._http.get(this.url + 'messages/' + page, {headers: headers})
    }  
    
    getSentMessages(token, page = 1):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json').set('Authorization', token);
        
        return this._http.get(this.url + 'sent-messages/' + page, {headers: headers})
    } 
    
    getConversation(token,user_id, page = 1):Observable<any>{
        let headers = new HttpHeaders().set('Content-Type', 'application/json').set('Authorization', token);
        
        return this._http.get(this.url + 'getConversation/' + user_id + '/' + page, {headers: headers})
    } 
    
    // deleteFollow(token, id):Observable<any>{
    //     let headers = new HttpHeaders().set('Content-Type', 'application/json').set('Authorization', token);
        
    //     return this._http.delete(this.url + 'follow/' + id, {headers: headers})
    // }
    
    // getFollowing(token, user_id = null, page = 1):Observable<any>{
    //     let headers = new HttpHeaders().set('Content-Type', 'application/json').set('Authorization', token);
    //     var url = this.url + 'following/';
    //     if(user_id!=null){
    //         url = this.url + 'following/' + user_id + '/' + page;
    //     }
    //     return this._http.get(url, {headers: headers})
    // }
    
    // getFollowed(token, user_id = null, page = 1):Observable<any>{
    //     let headers = new HttpHeaders().set('Content-Type', 'application/json').set('Authorization', token);
    //     var url = this.url + 'followed/';
    //     if(user_id!=null){
    //         url = this.url + 'followed/' + user_id + '/' + page;
    //     }
    //     return this._http.get(url, {headers: headers})
    // }    
    
    
    

}



