import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { User } from '../models/user';
import { Follow } from '../models/follow';
import { UserService } from '../services/user.service';
import { FollowService } from '../services/follow.service';
// import { UploadService } from '../services/upload.service';
import { GLOBAL } from '../services/global';

@Component({
  selector: 'users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css'],
  providers : [UserService, FollowService]
})
export class UsersComponent implements OnInit {
  public title: string;
  public identity;
  public token;
  public page;
  public nextPage;
  public prevPage;
  public status;
  public total;
  public follows;
  public followsme;
  public users: User[];
  public pages;
    public url:string;

  constructor(
    private _userService: UserService,
    private _route: ActivatedRoute,
    private _router: Router,
    private _followService: FollowService
    ) { 
    this.title = "Gente";
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
  }

  ngOnInit() {
    this.actualPage();
  }
  
  actualPage(){
    this._route.params.subscribe(params=>{
      let page = +params['page'];
      this.page = page;
      
      if(!page){
        page = 1;
        this.page = 1;
      }else{
        this.nextPage = page+1;
        this.prevPage = page-1;
        if(this.prevPage<=0){
          this.prevPage = 1;
        }
      }
      this.getUsers(page);
    });
  }
  
  getUsers(page){
    this._userService.getUsers(page).subscribe(response=>{
      if(!response.usuariosEncontrados){
        this.status = 'error';
      }else{
        console.log(response);
        this.users = response.usuariosEncontrados;  
        this.total = response.total;  
        this.pages = response.pages;  
        this.follows = response.follows;  
        if(page> this.page){
          this._router.navigate(['/gente', 1]);
        }
      }
      
    },
    error=>{
      console.log(error);
      this.status = 'error';
    })
  }
  
  public followUserOver;
  
  mouseEnter(user_id){
    this.followUserOver = user_id;
  }
  
  mouseLeave(user_id){
    this.followUserOver = 0;
  }
  
  followUser(followed){
    var follow = new Follow('', this.identity._id, followed);
    
    this._followService.addFollow(this.token, follow).subscribe(response=>{
      if(!response.follows){
        this.status="error";
      }else{
        this.status="success";
        this.follows.push(followed);
      }
    },
    error=>{
      console.log(error);
    })
  }
  
  unfollowUser(followed){
    this._followService.deleteFollow(this.token, followed).subscribe(response=>{
      console.log(response);
      var search = this.follows.indexOf(followed);
      if(search != -1){
        this.follows.splice(search, 1);
      }
    }, error=>{
      console.log(error);
    });
  }

}



