import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { User } from '../models/user';
import { UserService } from '../services/user.service';
import * as io from 'socket.io-client';

@Component({
  selector: 'login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [
    UserService
  ]
})
export class LoginComponent implements OnInit {
  public title: string;
  public user: User;
  public status: string;
  public identity;
  public token;
  public socket: SocketIOClient.Socket;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _userService: UserService) {
    this.title = "Identifícate";
    this.user = new User("", "", "", "", "", "", "", "",false);
    this.socket = io.connect();
  }

  ngOnInit() {
    // this.socket.on('usuariosConectados', (data: any) => {
    //   console.log(data);
    // });
  }

  sendForm(form) {
    this._userService.login(this.user).subscribe(
      response => {
        if (response.user) {
          this.identity = response.user;
          if (!this.identity || !this.identity._id) {
            this.status = 'error';
          }
          else {
            this.identity.conected = true;
            this.status = 'success';
            /*Guardar datos del usuario en localStorage*/
            localStorage.setItem('identity', JSON.stringify(this.identity));
            /*Conseguir el token*/
            this.getToken();
            this.socket.emit('loginUser', response.user);
          }
          // this.status = 'success';
          form.reset();
        }
        else {
          this.status = 'error';
        }

      },
      error => {
        this.status = 'error';
      }
    );
  }

  getToken() {
    this._userService.login(this.user, 'true').subscribe(
      response => {
        this.token = response.token;
        if (this.token.length <= 0) {
          this.status = 'error';
        }
        else {
          // this.status = 'success';
          /*PErsistir el token*/
          localStorage.setItem('token', this.token);
          /*Conseguir contadores y estadisticas del usuario*/
           this.getCounters();
        }

      },
      error => {
        this.status = 'error';
      }
    );
  }
  
  getCounters(){
    this._router.navigate(['/']);
    this._userService.getCounters(this.identity._id).subscribe(response =>{
      localStorage.setItem('stats', JSON.stringify(response.value));
      this.status = 'success';
      if(response.value.following.length){
        
      }
    },
    error =>{
      console.log(error);
      this._router.navigate(['/registro']);
    });
  }

}



