import { Component, OnInit, Input } from '@angular/core';
import { Publication } from '../models/publication';
import { UserService } from '../services/user.service';
import { UploadService } from '../services/upload.service';
import { PublicationService } from '../services/publication.service';
import { GLOBAL } from '../services/global';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'publications',
  templateUrl: './publications.component.html',
  styleUrls: ['./publications.component.css'],
  providers: [UserService, PublicationService]
})
export class PublicationsComponent implements OnInit {
  public identity;
  public token;
  public total;
  public pages;
  public user;
  public itemsPerPage;
  public page: number;
  public title: string;
  public loadedData;
  public url: string;
  public publications: Publication[];
  // @Input() user: string;

  constructor(
    private _router: Router,
    private _route: ActivatedRoute,
    private _userService: UserService,
    private _publicationService: PublicationService
  ) {
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.title = "Publicaciones";
    this.url = GLOBAL.url;
    this.page = 1;
  }

  ngOnInit() {
    this._route.params.subscribe(params => {
      let id = params['id'];
      this.user = params['id'];
      this.getPublicationsUser(this.user, this.page, false);
    });
  }

  getPublicationsUser(user, page, adding) {
    this._publicationService.getPublicationsUser(user, this.token, page).subscribe(response => {
      
      this.loadedData = true;
      this.total = response.total_items;
      this.pages = response.pages;
      this.itemsPerPage = response.items_per_page;
      if (!adding) {
        this.publications = response.publications;
      }
      else {
        let arrayA = this.publications;
        let arrayB = response.publications;
        this.publications = arrayA.concat(arrayB);
        $("html, body").animate({ scrollTop: $("html").prop("scrollHeight") }, 600);
      }
    }, error => {
      this.loadedData = true;
      console.log(error);
    });
  }
  public noMore = false;
  viewMore() {
    this.page += 1;
    if (this.page == this.pages) {
      this.noMore = true;
    }
    this.getPublicationsUser(this.user, this.page, true);
  }

  public publicationToDelete: Publication;

  setPublicationToDelete(publicacion) {
    this.publicationToDelete = publicacion;
  }

  borrarPublicacion(publication_id) {
    console.log("el id de la publicacion a bores ---> " + this.publicationToDelete);
    this._publicationService.deletePublication(this.token, this.publicationToDelete).subscribe(response => {
        this.getPublicationsUser(this.user, 1, false);
        this.noMore = false;
      },
      error => {
        console.log(error);
      })
  }

}



