import { Component, DoCheck } from '@angular/core';
import { UserService } from './services/user.service';
import { GLOBAL } from './services/global';
import { Router, ActivatedRoute, Params, NavigationStart, Event as NavigationEvent } from '@angular/router';
import * as io from 'socket.io-client';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [
    UserService
  ]
})
export class AppComponent implements DoCheck {
  public title: string;
  public identity;
  public remitente;
  public url = GLOBAL.url;
  public rutaActual: any;
  socket: SocketIOClient.Socket;

  constructor(
    private _userService: UserService,
    private _route: ActivatedRoute,
    private _router: Router) {
    this.title = 'Social Troy';

    _router.events.forEach((event: NavigationEvent) => {
      if (event instanceof NavigationStart) {
        this.rutaActual = event.url;
        console.log("La nueva ruta es ---> " + this.rutaActual);
      }
    });
  }

  ngOnInit() {
    this.socket = io.connect();
    this.identity = this._userService.getIdentity();

    this.socket.on('refreshChat', (data: any) => {
      console.log(data);
      // this.getConversation(this.receptor, 1, true, false);
      if (data.receiver._id == this.identity._id) {
        this.showAlertMessage(data);
      }
      else {

      }
    });
    
    this.socket.on('usuariosConectados', (data: any) => {
      console.log("Estos son los usuarios conectados en la web");
      console.log(data);
    });
    
    console.log(this.identity);
    
  }

  ngDoCheck() {
    this.identity = this._userService.getIdentity();
  }

  logout() {
    this.socket.emit('logout', this.identity);
    localStorage.clear();
    this.identity = null;
    this._router.navigate(['/login']);
  }

  showAlertMessage(user) {
    this.remitente = user.emiter;
    $('#toast').show();
    $('#toast').css('visibility', 'visible');
    setTimeout(function() {
      // $('#toast').hide();
      // $('#toast').css('visibility', 'hidden');
    }, 4000);
  }
}





