import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { User } from '../models/user';
import { UserService } from '../services/user.service';
import { UploadService } from '../services/upload.service';
import { GLOBAL } from '../services/global';

@Component({
  selector: 'app-user-edit',
  templateUrl: './user-edit.component.html',
  styleUrls: ['./user-edit.component.css'],
  providers: [
    UserService,
    UploadService
  ]
})
export class UserEditComponent implements OnInit {
  public title:string;
  public user:User;
  public identity;
  public token;
  public url = GLOBAL.url;
  public status:string;
  constructor(
    private _route: ActivatedRoute,
    private router: Router,
    private _userService: UserService,
    private _uploadService: UploadService) {
      this.title ="Actualizar mis datos";
      this.user = this._userService.getIdentity();
      this.identity = this.user;
      this.token = this._userService.getToken();
    }

  ngOnInit() {
    console.log('Componente Editar mis datos cargado');
    console.log(this.user);
  }
  
  sendForm(){
    this._userService.updateUser(this.user).subscribe(response=>{
      console.log(response);
      if(!response.user){
        this.status ='error';
      }else{
        this.status ='success';
        localStorage.setItem('identity', JSON.stringify(this.user));
        this.identity = this.user;
        /*Subida imagen de usuario*/
        this._uploadService.makeFileRequest(this.url+ 'upload-image-user/' + this.user._id, [], this.filesToUpload, this.token, 'image').then((result:any)=>{
          console.log(result);
          this.user.image = result.user.image;
          localStorage.setItem('identity', JSON.stringify(this.user));
        })
      }
    }, 
    error=>{
      console.log(error);
    })
  }
  
  public filesToUpload : Array<File>;
  fileChangeEvent(fileInput: any){
    this.filesToUpload = <Array<File>>fileInput.target.files;
    console.log(this.filesToUpload);
  }

}



