import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { User } from '../models/user';
import { Follow } from '../models/follow';
import { UserService } from '../services/user.service';
import { FollowService } from '../services/follow.service';
// import { UploadService } from '../services/upload.service';
import { GLOBAL } from '../services/global';

@Component({
  selector: 'following',
  templateUrl: './followed.component.html',
  styleUrls: ['./followed.component.css'],
  providers : [UserService, FollowService]
})
export class FollowedComponent implements OnInit {
  public title: string;
  public identity;
  public token;
  public page;
  public nextPage;
  public prevPage;
  public status;
  public total;
  public follows;
  public following;
  public followsme;
  public users: User[];
  public pages;
  public url:string;
  public userPageId;
  public followingIds;
  public user:User;

  constructor(
    private _userService: UserService,
    private _route: ActivatedRoute,
    private _router: Router,
    private _followService: FollowService
    ) { 
    this.title = "Usuarios que siguen a ";
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
  }

  ngOnInit() {
    console.log("Se cargo el componente following");
    this.actualPage();
  }
  
  actualPage(){
    this._route.params.subscribe(params=>{
      let user_id = params['id'];
      this.userPageId = user_id
      let page = +params['page'];
      this.page = page;
      this.getUser(user_id);
      if(!page){
        page = 1;
      }else{
        this.nextPage = page+1;
        this.prevPage = page-1;
        if(this.prevPage<=0){
          this.prevPage = 1;
        }
      }
      this.getFollows(user_id, page);
    });
  }
  
  getFollows(user_id, page){
    this._followService.getFollowed(this.token, user_id, page).subscribe(response=>{
      if(response && !response.follows){
        this.status = 'error';
      }else{
        // this.users = response.usuariosEncontrados;  
        this.total = response.total;  
        this.pages = response.pages;  
        this.followsme = response.follows;  
        this.followingIds = response.users_followed;
        this.follows = response.users_following;  
        if(page> this.page){
          this._router.navigate(['/gente', 1]);
        }
      }
      
    },
    error=>{
      console.log(error);
      this.status = 'error';
    })
  }
  
  public followUserOver;
  
  mouseEnter(user_id){
    this.followUserOver = user_id;
  }
  
  mouseLeave(user_id){
    this.followUserOver = 0;
  }
  
  followUser(followed){
    var follow = new Follow('', this.identity._id, followed);
    
    this._followService.addFollow(this.token, follow).subscribe(response=>{
      if(!response.follows){
        this.status="error";
      }else{
        this.status="success";
        // this.followsme.push(response.follows.followed);
        this.followingIds.push(response.follows.followed);
        // this.getFollows(this.userPageId, 1);
      }
    },
    error=>{
      console.log(error);
    })
  }
  
  unfollowUser(followed){
    this._followService.deleteFollow(this.token, followed).subscribe(response=>{
      var search = this.followingIds.indexOf(followed);
      if(search != -1){
        this.followingIds.splice(search, 1);
      }
    }, error=>{
      console.log(error);
    });
  }
  public userName;
  getUser(id){
    this._userService.getUser(id).subscribe(response=>{
      if(response.user){
        this.user = response.user;  
        this.userName = response.user.name;
      }else{
        this._router.navigate(['/home'])
      }
    },
    error=>{
      console.log(error);
    });
  }

}



