import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { User } from '../models/user';
import { Follow } from '../models/follow';
import { UserService } from '../services/user.service';
import { FollowService } from '../services/follow.service';
import { GLOBAL } from '../services/global';
import { UploadService } from '../services/upload.service';

@Component({
  selector: 'profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css'],
  providers: [UserService, FollowService, UploadService]
})
export class ProfileComponent implements OnInit {
  public title;
  public status;
  public identity;
  public token;
  public url;
  public stats;
  public followed;
  public following;
  public user: User;
  public loadedData;
  public showPerfilImage;
  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _userService: UserService,
    private _followService: FollowService,
    private _uploadService: UploadService
  ) {
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.title = 'Perfil';
    this.url = GLOBAL.url;
    this.followed = false;
    this.following = false;

  }

  ngOnInit() {
    this.loadPage();
  }

  loadPage() {
    this._route.params.subscribe(params => {
      let id = params['id'];
      this.getUser(id);
      this.getCounters(id);
      this.user = params['id'];
    });

  }

  getUser(id) {
    this._userService.getUser(id).subscribe(response => {
        if (response.user) {
          this.user = response.user;
          this.title = 'Perfil de ' + this.user.nick;
          this.loadedData = true;
          if (response.following) {
            if (response.following._id) {
              this.following = true;
            }
            else {
              this.following = false;
            }
          }
          if (response.followed) {
            if (response.followed._id) {
              this.followed = true;
            }
            else {
              this.followed = false;
            }
          }


        }
        else {
          this.status = "error";
        }
      },
      error => {
        console.log(error);
        this._router.navigate(['/perfil', this.identity._id])
      });
  }

  getCounters(id) {
    this._userService.getCounters(id).subscribe(response => {
        this.stats = response.value;
        console.log('ESDTO SON LOS STATS');
        console.log(response);
      },
      error => {
        console.log(error);
      })
  }

  followUser(followed) {
    var follow = new Follow('', this.identity._id, followed);

    this._followService.addFollow(this.token, follow).subscribe(response => {
        console.log(response);
        this.following = true
      },
      error => {
        console.log(error);
      })
  }

  unfollowUser(followed) {
    var follow = new Follow('', this.identity._id, followed);

    this._followService.deleteFollow(this.token, followed).subscribe(response => {
        console.log(response);
        this.following = false
      },
      error => {
        console.log(error);
      })
  }

  public followUserOver;
  mouseEnter(user_id) {
    this.followUserOver = user_id;
  }

  mouseLeave() {
    this.followUserOver = 0;
  }

  sendForm() {
    this._userService.updateUser(this.user).subscribe(response => {
        console.log(response);
        if (!response.user) {
          this.status = 'error';
        }
        else {
          this.status = 'success';
          localStorage.setItem('identity', JSON.stringify(this.user));
          this.identity = this.user;
          /*Subida imagen de usuario*/
          this._uploadService.makeFileRequest(this.url + 'upload-image-user/' + this.user._id, [], this.filesToUpload, this.token, 'image').then((result: any) => {
            console.log(result);
            this.user.image = result.user.image;
            localStorage.setItem('identity', JSON.stringify(this.user));
          })
        }
      },
      error => {
        console.log(error);
      })
  }

  public filesToUpload : Array<File>;
  fileChangeEvent(fileInput: any){
    this.filesToUpload = <Array<File>>fileInput.target.files;
    console.log(this.filesToUpload);
  }

}



